.modal-about {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 28px;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  width: 568px;
  //   height: 459px;
  // height: 100vh;

  border: 2px solid var(--acc-color);
  border-radius: 10px;
}

.modal-about__name {
  font-family: 'Seymour One';
  font-style: normal;
  text-align: center;
  @include font(16px, 400, 24px);
  margin-bottom: 20px;
}

iframe {
  width: 100%;
  height: 199px;
}

.modal-about__video {
  margin-bottom: 20px;
}

.modal-about__description {
  @include font(14px, 400, 20px);
}

@media screen and (max-width: 767px) {
  .modal-about {
    width: 100%;
    max-width: 280px;
  }
}
@media screen and (min-width: 768px) {
  .modal-about {
    padding: 32px 60px;
  }
  .modal-about__name {
    @include font(20px, 400, 28px);
    margin-bottom: 32px;
  }
  iframe {
    width: 100%;
    height: 205px;
  }
  .modal-about__description {
    @include font(16px, 400, 22px);
  }
}
